<template>
  <div class="mainBox flex">
    <div class="left">
      <button @click="connect">连接</button>
      <button @click="exit">断开</button>
      <div :class="self.status === 'onLine'?'onLine':'offline'"></div>
      <span>{{ self.clientName }}</span>
      <div class="member">在线列表👇：</div>
      <div class="fx-ver memberList">
        <div class="member" v-for="(item,idx) in memberList.list" :key="idx">
          <span>{{ item }}</span>
        </div>
      </div>
    </div>
    <div class="right fx-ver">
      <div class="chatName">
        <p>大型多人激情聊天现场</p>
      </div>
      <div class="top" id="talkBox">
        <div class="oneLine" v-for="(item,idx) in messageList" :key="idx">
          <Message :item=item></Message>
        </div>
      </div>
      <div class="down">
        <textarea v-model="text" style="width: 99%;height: 97%" @keyup.enter="sendMsg">
        </textarea>
      </div>
    </div>
  </div>
</template>

<script>
import client from '../utils/ConnectWebSocket'
import Message from './Message'

export default {
  name: "ChatRoom",
  components: {Message},
  data() {
    return {
      messageList: client.getMessageList(),
      self: client.getSelf(),
      memberList: client.getMemberList(),
      auth: null,
      text: "",
      talkBox: null,
    }
  },
  created() {
    global.view = this;
  },
  mounted() {
    this.talkBox = document.getElementById('talkBox');
  },
  methods: {
    exit() {
      if (this.self.status === 'offline') {
        this.$message.error("未登录！");
        return;
      }
      client.close();
    },
    connect() {
      if (this.self.status === 'onLine') {
        this.$message.error("已登录！");
        return;
      }
      this.messageList.length = 0;
      client.getConnection("chatroom");
      client.on("data", this.onRecvData)
    },
    sendMsg() {
      let Msg = {
        msg: this.text,
        sendTime: Date.now()
      }
      this.text = '';
      client.writeData(JSON.stringify(Msg));
    },
    onRecvData(event) {
      let data = event.data;
      let Msg = JSON.parse(data);
      switch (Msg.type) {
        case "talk": {
          Msg.self = Msg.clientName === this.self.clientName;
          this.messageList.push(Msg);
          break;
        }
        case "auth": {
          client.setSelf(Msg.clientName);
          break;
        }
        case "upLine": {
          this.memberList.list.push(Msg.clientName);
          this.memberList.list = [...this.memberList.list];
          break;
        }
        case "offLine": {
          client.oneOffLine(Msg.clientName);
          break;
        }
      }
      setTimeout(() => {
        this.talkBox.scrollTop = this.talkBox.scrollHeight;
      }, 250);
    }
  }
}
</script>

<style scoped>

.mainBox {
  margin-top: 100px;
  width: 975px;
  height: 705px;
  background-color: #f3f5f5;
  border: #29C449 1px solid;
}

.left {
  width: 30%;
  border-right: #29C449 1px solid;
}

.right {
  width: 70%;
}

.top {
  height: 70%;
  border-bottom: #29C449 1px solid;
  overflow-x: scroll;

}

.chatName {
  border-bottom: #29C449 1px solid;
  padding: 10px;
}

::-webkit-scrollbar {
  width: 8px;
  background-color: #F5F5F5;
}

.down {
  height: 30%;
}

.oneLine {
  margin-top: 30px;
}

.onLine {
  border-radius: 10px;
  background-color: #29c449;
  width: 20px;
  height: 20px;
  display: inline-block;
}

.offline {
  border-radius: 10px;
  background-color: red;
  width: 20px;
  height: 20px;
  display: inline-block;
}

.memberList {
  width: 100%;
  overflow: scroll;
}

.member {
  margin: 10px;
  border: #29c449 1px solid;
  padding: 3px;
}
</style>