class WsClient {
  constructor() {
    this.client = null;
    this.messageList = [];
    this.memberList = {
      list: []
    };
    this.self = {
      clientName: '---',
      status: 'offline'
    }
  }
  
  getConnection(connName) {
    this.client = new WebSocket(`ws://${window.host || '150.158.147.117'}:8989/ws`);
    //open event
    this.client.onopen = function () {
      console.log(connName + " open websocket...");
    };
    //close event
    this.client.onclose = () => {
      this.self.clientName = '---';
      this.self.status = 'offline';
      this.memberList.list = [];
      console.log(connName + " close websocket...");
    };
    
    this.client.onerror = () => {
      this.self.clientName = '---';
      this.self.status = 'offline';
      this.memberList.list = [];
      console.log(connName + " error...");
    }
  }
  
  close() {
    this.client.close();
  }
  
  on(type, func) {
    if (type !== 'data') return;
    // 响应onmessage事件:
    this.client.onmessage = func;
  }
  
  writeData(data) {
    this.client.send(data);
  }
  
  getMessageList() {
    return this.messageList;
  }
  
  getMemberList() {
    return this.memberList;
  }
  
  oneOffLine(name) {
    this.memberList.list = this.memberList.list.filter(value => value !== name);
  }
  
  getSelf() {
    return this.self;
  }
  
  setSelf(name) {
    this.self.clientName = name;
    this.self.status = 'onLine';
  }
}

const client = new WsClient();
global.client = client;
export default client